.arrow-logo {}
.arrow-logo--to-right {
	-webkit-transform: rotateZ(180deg);
	    -ms-transform: rotate(180deg);
	        transform: rotateZ(180deg);
}
.arrow-logo--to-top {
	-webkit-transform: rotateZ(90deg);
	    -ms-transform: rotate(90deg);
	        transform: rotateZ(90deg);
}
.arrow-logo--to-bottom {
	-webkit-transform: rotateZ(-90deg);
	    -ms-transform: rotate(-90deg);
	        transform: rotateZ(-90deg);
}



