.app-layout {
	color: var(--white-text);
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	height: 100%;
	width: 100%;
}

.app-layout__main {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-positive: 2;
	    flex-grow: 2;
	position: relative;
	width: 100%;
}

.app-layout__header {
	background: transparent;
	position: fixed;
	top: 8px;
	left: 8px;
	z-index: 99999;
}
.app-layout__footer {}


