.drop-down-menu {
	-ms-flex-align: center;
	    align-items: center;
	display: -ms-inline-flexbox;
	display: inline-flex;
	position: relative;
	list-style: none none;
	height: 100%;
	margin: 0;
	padding: 0;
}

.drop-down-menu:hover .drop-down-menu__menu {
	display: block;
}

.drop-down-menu:hover .drop-down-menu__toggler {
	border-radius: 3px;
	background: var(--gray);
	cursor: pointer;
}

.drop-down-menu:hover .drop-down-menu__caret{
	color: var(--white-text);
}

.drop-down-menu__toggler {
	display: -ms-flexbox;
	display: flex;
	padding: 5px 8px;
}

.drop-down-menu__caret {
	color: var(--gray);
	font-size: 10px;
	margin-left: 8px;
	margin-top: 2px;
}

.drop-down-menu__menu {
	border: var(--gray) 1px solid;
	background-color: var(--black);
	display: none;
	left: 0;
	list-style: none none;
	margin: 11px 0 0 0;
	padding: 0;
	min-width: 100px;
	position: absolute;
	z-index: 99999;
}

.drop-down-menu__menu-item {
	display: -ms-flexbox;
	display: flex;
	padding: 8px;
	cursor: pointer;
	width: 100%;
	white-space: nowrap;
}
.drop-down-menu__menu-item:hover {
	background: var(--gray);
}

.drop-down-menu__menu-item-icon {
	min-width: 35px;
}


